#header {			
    // burger
    .burger {
        position: relative;
        width: 50px;
        height: 22px;
        cursor: pointer;
        z-index: 2;
        right: 20px;
        top: 20px;
        position: fixed;
        top:20px;
        left: 20px;
        z-index: 3;
    }
    .burger::before, .burger::after {
        content: "";
        width: 100%;
        height: 3px;
        background: #fff;
        position: absolute;
        right: 0;
        -webkit-transition: 0.4s cubic-bezier(0.785, 0.135, 0.15, 0.86);
        -o-transition: 0.4s cubic-bezier(0.785, 0.135, 0.15, 0.86);
        transition: 0.4s cubic-bezier(0.785, 0.135, 0.15, 0.86);
    }
    .burger::before {
        bottom:0;
    }
    .burger::after {
        top:0;
    }
    .change:before {
            bottom: 50%;
            -webkit-transform: rotate(-45deg) translate3d(0, 50%, 0);
            transform: rotate(-45deg) translate3d(0, 50%, 0);
        }
    .change:after {
        top: 50%;
        -webkit-transform: rotate(45deg) translate3d(0, -50%, 0);
        transform: rotate(45deg) translate3d(0, -50%, 0);
    }
    // menu
    .menu {
        
        background: #636077;
        width: 10%;
        height: 100vh;
        display: flex;
         flex-direction: column;
        font-size: 30px;
        font-family: verdana;
        position: fixed;
        font-size: 1.3em;//font-size: 60px;
        align-items: center;
        //display: none;
        justify-content: center;
        z-index:1;
        left:-10%;
        opacity: 0;
        a {
            width: 100%;
            text-align: center;
            height: 80px;
            line-height: 80px;
            text-decoration: none;
            color: white;
            font-size: 30px;
            .menuItem {
                width: 100%;
                text-align: center;
                height: 50px;
                line-height: 50px;
            
                
            }
        }
        a:hover {
            color:#225527;
        }
    }
    .showMenu {
        animation-name: move;
        animation-duration: 1s;
        animation-fill-mode: forwards;
        z-index: 2;
    }
    .hideMenu {
        animation-name: hide;
        animation-duration: 1s;
        animation-fill-mode: forwards;        
        z-index: 2;
    }
    @keyframes move {
        from {left: -10%;}
        to {left: 0;}
    }
    @keyframes hide {
        from {left: 0;}
        to {left: -10%;}
    }
}
